<template>
  <div class="footer">
    {{footerData}}
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  name: 'footer-view',
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters([
      'footerData'
    ])
  }
}
</script>

<style>
.footer {
  color: #000;
  height: 50px;
  width: 100%;
  border-top:solid 1px #393939;
  background-color: #fff;
  bottom: 0;
  position: fixed;
  z-index: 13000;
}
</style>



